.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-pic {
 max-width: 300px; 
 height: 100%; 
 overflow: hidden;
}

.subsection  {
    margin: 40px;
    text-align: left;
    padding-top: 55px;
    padding-left: 0 10%;
    padding-right: 0 10%;
    padding-bottom: 15px;
}

.header {
    position: fixed; 
    overflow: hidden;
    top: 0;
    padding-left: 0 5%;
    padding-right: 0 5%;
    width: 100%;
    z-index: 999;
}

.content {
    position: relative;
    padding-top: 55px;
}

.vertical-center {
  display: flex;
  align-items: center; 
}

.section-title {
    margin-top: 20px;
    margin-bottom: 20px;
}

.project {
    padding-top: 30px;
    padding-bottom: 30px;
}

